import React from "react";

import "./ContentSrcViewer.css";

const ContentSrcViewer = (props) => {
  var { className, ...rest } = props;
  if (className == undefined) {
    className = "ContentSrcViewer";
  }
  else {
    className += " ContentSrcViewer";
  }
  return (
    <div className={className} {...rest}>
      {props.children}
    </div>
  );
};

export default ContentSrcViewer;