import React from "react";

import "./ContentArea.css";

const ContentArea = (props) => {
  var { className, ...rest } = props;
  if (className == undefined) {
    className = "ContentArea";
  }
  else {
    className += " ContentArea";
  }
  return (
    <div className={className} {...rest}>
      {props.children}
    </div>
  );
};

export default ContentArea;