import React from "react";

import "./ContentBox.css";

const ContentBox = (props) => {
  var { className, ...rest } = props;
  if (className == undefined) {
    className = "ContentBox";
  }
  else {
    className += " ContentBox";
  }
  return (
    <div className={className} {...rest}>
      {props.children}
    </div>
  );
};

export default ContentBox;