import React, { Component } from 'react';

import './DonatePage.css';

import { Link } from "react-router-dom";

import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import ButtonArea from "../../components/Buttons/ButtonArea/ButtonArea.js";
import LinkButton from "../../components/Buttons/LinkButton/LinkButton.js";



export default class DonatePage extends Component {
    T(en, ko) {
        if (this.props.language == "en") {
          return en;
        }
        else {
          return ko;
        }
    }

    download = undefined;

    render() {
        if (this.props.language == "en") {
            var donationButtons = (
                <>
                    <LinkButton href="https://twip.kr/donate/devhotteok" target="_blank">Twip</LinkButton>
                    <LinkButton href="https://paypal.me/devhotteok" target="_blank">PayPal</LinkButton>
                    <LinkButton href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</LinkButton>
                    <LinkButton href="https://toon.at/donate/devhotteok" target="_blank">Toonation</LinkButton>
                </>
            );
        }
        else {
            var donationButtons = (
                <>
                    <LinkButton href="https://twip.kr/donate/devhotteok" target="_blank">트윕</LinkButton>
                    <LinkButton href="https://toon.at/donate/devhotteok" target="_blank">투네이션</LinkButton>
                    <LinkButton href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</LinkButton>
                </>
            );
        }
        var checkUrl = new URL(window.location.href);
        var downloadVersion = checkUrl.searchParams.get("download");
        var availableVersions = ["1.0.0", "1.0.1", "1.0.2", "1.0.3", "1.0.4"];
        if (availableVersions.indexOf(downloadVersion) == -1) {
            downloadVersion = undefined;
        }
        if (downloadVersion != undefined) {
            this.download = (
                <div className="downloadInProgress">
                    <Label>{this.T("Thanks for using TwitchLink!", "TwitchLink를 이용해 주셔서 감사합니다.")}</Label>
                    <Label>{this.T("Your download will begin shortly.", "곧 다운로드가 시작됩니다.")}</Label>
                    <LinkButton style={{margin: "10px auto"}} id="download" href={"https://github.com/devhotteok/TwitchLink/releases/download/" + downloadVersion + "/TwitchLink-" + downloadVersion + ".exe"}>
                        {this.T("If the download doesn't start automatically, click here.", "다운로드가 진행되지 않는다면 이곳을 누르세요.")}
                    </LinkButton>
                    <Link className="LinkButton helpButton" to={"/help?lang=" + this.props.language} target="_blank">
                        {this.T("Having trouble installing?", "설치하는데 문제가 있나요?")}
                    </Link>
                </div>
            );
        }
        return (
            <div className="DonatePage">
                <PageFrame>
                    <Content.TitleBar>{this.T("Donate", "후원")}</Content.TitleBar>
                    {this.download}
                    <Label className="donateInfo">{this.T("If you like the program, please consider contributing to TwitchLink!", "프로그램이 마음에 드신다면 TwitchLink 개발을 도와주세요!")}</Label>
                    <ButtonArea>
                        {donationButtons}
                    </ButtonArea>
                    <Label className="thankyou">{this.T("Thanks For Your Help!", "후원해주셔서 감사합니다.")}</Label>
                </PageFrame>
            </div>
        );
    }

    componentDidMount() {
        if (this.download != undefined) {
            setTimeout(function () {
                document.getElementById("download").click();
            }, 1000);
        }
    }
}